html {
  font-size: 62.5%; }

body {
  background: #FFF;
  color: black;
  line-height: 1.4;
  @include f(l, 16);
  @include smooth-font; }

a {
  text-decoration: underline;
  color: $color-blue;
  @include trans(color);
  &:hover,
  &:focus {
    color: red; } }

.text {
  @include f(l, 16);
  line-height: 1.5625;
  p {
    margin: 1em 0;
    &:first-child {
      margin-top: 0; }
    &:last-child {
      margin-bottom: 0; } }
  a {
    &:not(:hover) {
      color: inherit; } } }

.header {
  position: relative; }

.header__inner {
  width: 80%;
  margin: auto;
  box-sizing: border-box;
  padding: 0 0 0 percentage(165/1200);
  position: relative; }

.lang {
  position: absolute;
  top: 26px;
  right: 33px;
  a {
    &:not(:hover) {
      color: inherit; } }
  &:hover {
    .lang__list {
      opacity: 1;
      visibility: visible; } } }

.lang__list {
  opacity: 0;
  visibility: hidden;
  position: absolute;
  z-index: 1;
  top: 0;
  left: 0;
  white-space: nowrap;
  background: white;
  @include trans; }

.nav {
  padding: 22px 0 0;
  margin: 0 0 85px 0; }

.nav__item {
  display: inline-block;
  vertical-align: top;
  margin-right: 20px;
  line-height: 1.7; }

.nav__link {
  display: inline-block;
  @include f(l, 16); }


.logo {
  display: block;
  position: absolute;
  top: 28px;
  left: 0;
  color: black;
  @include f(b, 16, 100);
  text-decoration: none;
  text-transform: uppercase;
  line-height: 1; }

.lang {
  @include f(l, 16);
  color: rgba(black, 0.3); }

.link {
  color: $color-blue;
  text-decoration: none;
  border-bottom: 1px dotted;
  line-height: 1;
  cursor: pointer;
  @include trans(color);
  &:hover {
    color: red; } }

.hero {
  position: relative;
  @include f(el, 16);
  line-height: 1.526;
  padding: 0 45% 95px 0;
  position: relative;
  p {
    margin: 1em 0; }
  .btn {
    margin-top: 15px; } }

.hero-figure {
  position: absolute;
  z-index: -1;
  top: -40px;
  right: 0;
  visibility: hidden;
  opacity: 0;
  &-iptv {
    .hero-figure__text {
      width: 330px; } }
  &-ipcam {
    top: -24px;
    right: 40px;
    .hero-figure__text {
      width: 380px; } }
  &.is-active {
    visibility: visible;
    opacity: 1; } }

.hero-figure__text {
  position: absolute;
  top: 320px;
  right: 0;
  @include f(el, 14);
  line-height: 1.43;
  color: $color-blue; }

.hero-links {
  margin: 23px 0; }

.hero-btn {
  display: inline-block;
  margin-right: 15px;
  text-decoration: none;
  @include f(el, 16);
  color: inherit;
  @include trans(color);
  line-height: 1;
  color: $color-blue;
  cursor: pointer;
  user-select: none;
  > * {
    display: inline-block;
    vertical-align: middle; }
  span {
    border-bottom: 1px dotted; }
  g {
    stroke: currentColor; }
  circle {
    visibility: hidden;
    opacity: 0;
    @include trans; }
  &:hover {
    color: red; }
  &.is-active {
    color: black;
    cursor: default;
    span {
      border-color: rgba(black, 0); }
    circle {
      visibility: visible;
      opacity: 1; } } }


.title {
  @include f(el, 50);
  line-height: 1.1;
  &_bold {
    @include f(l); } }


.section {
  margin-top: 70px;
  .title {
    margin-bottom: 30px; } }

.section-request {
  margin-top: 100px; }

.section-contacts {
  margin-top: 65px;
  background: #002FC7;
  color: white;
  padding: 60px 0 55px;
  @include clr;
  .svg-map-marker {
    margin: 5px 0 8px -7px;
    font-size: 35px;
    fill: white; }
  .title {
    margin-bottom: 16px; }
  .social-media {
    float: right;
    margin-top: 5px; } }

.section-contacts__cell {
  float: left;
  width: 50%;
  box-sizing: border-box;
  padding-right: 40px;
  &:nth-child(2) {
    max-width: 390px; } }

.btn {
  display: inline-block;
  box-sizing: border-box;
  height: 45px;
  min-width: 140px;
  padding: 0 10px;
  line-height: 43px;
  border: 1px dotted;
  border-radius: 3px;
  background: none;
  @include f(el, 16);
  color: $color-blue;
  @include trans;
  transition-property: color, background;
  text-decoration: none;
  text-align: center;
  cursor: pointer;
  &:hover {
    color: red; }
  &_fluid {
    display: block;
    width: 100%; }
  &_solid {
    background: $color-blue;
    border: none;
    color: white;
    height: 50px;
    line-height: 50px;
    @include f(eb, 14, 150);
    text-transform: uppercase;
    &:hover {
      color: white;
      background: red; } } }

.features {
  display: none;
  position: relative;
  min-height: 980px;
  padding: 100px 40px 40px;
  box-sizing: border-box;
  background: #001E8D;
  color: white;
  &-ipcam {
    overflow: hidden;
    .features__center {
      padding-left: 80px; } }
  // &-iptv
  &.is-active {
    display: block; } }

.features__center {
  max-width: 420px;
  margin: auto;
  box-sizing: content-box;
  position: relative;
  z-index: 1; }

.feature {
  margin-top: 80px;
  max-width: 245px;
  &:nth-child(even) {
    margin-left: 80px; } }

.feature__label {
  @include f(m, 16);
  line-height: 1.526; }

.feature__text {
  @include f(el, 14);
  line-height: 1.78; }

.feature-pic {
  position: absolute; }

.onvif {
  right: 50%;
  top: 90px;
  margin-right: percentage(200/1200); }

.motion {
  top: 210px;
  left: 50%;
  margin-left: percentage(220/1200);
  width: 240px;
  height: 240px;
  &__item {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    &:before {
      content: "";
      display: block;
      width: 100%;
      height: 100%;
      background: img-url('motion1.png') no-repeat center;
      background-size: contain; } }
  &__1 {
    // background: img-url('motion1.png') no-repeat center
    // width: 240px
    // height: 240px
    // margin: -120px 0 0 -120px
    animation: spin 4s linear infinite; }
  &__2 {
    // background: img-url('motion2.png') no-repeat center
    // width: 152px
    // height: 152px
    // margin: -76px 0 0 -76px
    top: 10px;
    left: -120px;
    animation: spinback 3s linear infinite;
    &:before {
      transform: scale(-0.63, 0.63); } }
  &__3 {
    // background: img-url('motion3.png') no-repeat center
    // width: 82px
    // height: 82px
    // margin: -41px 0 0 -41px
    top: 37px;
    left: -17px;
    animation: spinback 3.5s linear infinite;
    &:before {
      transform: scale(-0.34, 0.34); } } }

@keyframes spin {
  to {
    transform: rotate(360deg); } }

@keyframes spinback {
  to {
    transform: rotate(-360deg); } }

.web-access {
  width: 393px;
  height: 288px;
  top: 517px;
  right: 50%;
  margin-right: percentage(150/1200);
  background: img-url('mouse.png') no-repeat center;
  background-size: contain; }

.mobile-app {
  top: 638px;
  left: 50%;
  width: 587px;
  height: 519px;
  margin-left: percentage(140/1200);
  background: img-url('smartphone.png') no-repeat center;
  background-size: contain; }

.clouds {
  top: 60px;
  right: 50%;
  // margin-right: percentage(230/1200)
  margin-right: percentage(140/1200);
  width: 500px;
  height: 300px;
  &__cloud {
    position: absolute;
    top: 0;
    left: 0;
    width: 270px;
    height: 270px;
    background: img-url('cloud.png') no-repeat center;
    background-size: contain;
    animation: cloud 7s linear infinite;
    @for $i from 1 through 20 {
      &:nth-child(#{$i}) {
        animation-delay: random(10) + s; } } }
  &__cloud_2 {
    background-image: img-url('cloud2.png'); }
  &__cloud_3 {
    background-image: img-url('cloud3.png'); }
  &__logo-amazon {
    position: absolute;
    top: 26px;
    left: 80px;
    background: img-url('amazon.png') no-repeat center;
    background-size: contain;
    width: 140px;
    height: 55px; }
  &__logo-openstack {
    position: absolute;
    top: 70px;
    left: 250px;
    background: img-url('openstack.png') no-repeat center;
    background-size: contain;
    width: 86px;
    height: 82px; }

  &__layer-1 {
    top: -30px;
    left: 30px; }
  &__layer-2 {
    top: 0;
    left: -60px;
    opacity: 0.7;
    // transform: scale(0.7)
    background-size: 70%; }
  &__layer-3 {
    top: 60px;
    left: -30px;
    // transform: scale(0.7)
    background-size: 70%; }
  &__layer-4 {
    top: 48px;
    left: 90px;
    opacity: 0.7; }
  &__layer-5 {
    top: 40px;
    left: auto;
    right: 28px;
    // transform: scale(0.8)
    background-size: 80%; }
  &__layer-6 {
    top: 40px;
    left: auto;
    right: -55px;
    // transform: scale(0.7)
    background-size: 70%;
    opacity: 0.7; }
  &__layer-7 {
    top: 70px;
    left: auto;
    right: 23px;
    // transform: scale(0.8)
    background-size: 80%;
    opacity: 0.6; }
  &__layer-8 {
    top: 90px;
    left: 80px;
    // transform: scale(0.7)
    background-size: 70%; } }

@keyframes cloud {
  0%, 100% {
    opacity: 1;
    transform: translate3d(0,0,0); }
  50% {
    opacity: 0;
    transform: translate3d(50px,0,0); }
  51% {
    opacity: 0;
    transform: translate3d(-50px,0,0); } }

.transcoder {
  right: 50%;
  margin-right: percentage(220/1200);
  top: 465px;
  width: 290px;
  height: 400px;
  background: img-url('svg/transcoder.svg') no-repeat center;
  background-size: contain;
  &__lightning {
    width: 100px;
    height: 100px;
    position: absolute;
    top: 50%;
    left: 50%;
    margin: -52px 0 0 -44px; } }

.hdd {
  top: 530px;
  left: 50%;
  margin-left: percentage(170/1200);
  width: 395px;
  height: 540px;
  background: img-url('hdd.png') no-repeat center;
  background-size: contain;
  &:before {
    content: "";
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: img-url('hdd-lights.png') no-repeat center;
    animation: blink 2s ease-in-out infinite; } }

@keyframes blink {
  0%, 100% {
    opacity: 0; }
  50% {
    opacity: 1; } }

.lego {
  top: 320px;
  left: 50%;
  margin-left: percentage(178/1200);
  &__brick-upper {
    position: absolute;
    z-index: 1;
    top: -90px;
    left: 65px;
    width: 267px;
    height: 202px;
    background: img-url('lego-blue.png') no-repeat center;
    background-size: contain;
    transform: translateY(-50px); }
  &__brick-lower {
    position: relative;
    z-index: 0;
    width: 267px;
    height: 205px;
    background: img-url('lego-orange.png') no-repeat center;
    background-size: contain;
    &:before {
      content: "";
      position: absolute;
      top: -105px;
      left: 33px;
      width: 100%;
      padding-bottom: percentage(350/340);
      background: img-url('lego-shadow.png') no-repeat center; } } }

.form {
  .btn {
    margin-top: 44px; } }

.field {
  margin: 20px 0;
  &:first-child {
    margin-top: 0; }
  &:last-child {
    margin-bottom: 0; } }

.input {
  display: block;
  box-sizing: border-box;
  height: 40px;
  padding: 10px 0 9px;
  width: 100%;
  appearance: none;
  background: none;
  border: none;
  border-bottom: 1px solid rgba(black, 0.5);
  @include f(r, 20);
  color: black;
  resize: none;
  @include placeholder {
    @include f(l, 16, 150);
    text-transform: uppercase;
    color: #9D9CA7; } }

textarea.input {
  max-height: 200px;
  line-height: 20px; }


.news {
  line-height: 1.5625;
  &:not(:last-child) {
    margin-bottom: 25px; }
  &_important {
    .news__title {
      font-size: 25px;
      @include f(l); }
    .news__text {
      font-size: 22px;
      margin: 10px 0; }
    .news__date {
      font-size: 16px; } } }

.news__title {
  @include f(r); }

.news__date {
  display: block;
  font-size: 14px;
  color: #999999; }


.message {
  display: block;
  padding: 10px;
  box-sizing: border-box;
  text-align: center;
  @include f(l, 16);
  line-height: 1.6;
  background: #8CFFB7;
  border-radius: 2px; }

.customers__list {
  font-size: 0;
  margin: 0 -15px -40px; }

.customers__list-item {
  display: inline-block;
  vertical-align: top;
  font-size: 16px;
  width: 33.33%;
  box-sizing: border-box;
  padding: 0 15px;
  margin-bottom: 40px; }

.customer__pic {
  height: 60px;
  margin-bottom: 10px;
  line-height: 60px;
  font-size: 0;
  > img {
    display: inline-block;
    vertical-align: middle;
    max-height: 100%;
    max-width: 100%; } }

.customer__link {
  display: inline-block;
  @include f(u, 11, 180);
  text-transform: uppercase; }

.icon-link {
  display: inline-block;
  vertical-align: top;
  margin-right: 40px;
  color: $color-blue;
  text-decoration: underline;
  line-height: 1.375;
  @include trans(color);
  .svg-icon {
    display: block;
    font-size: 40px;
    fill: currentColor;
    margin-bottom: 5px; }
  &:hover {
    color: red; } }

.social-link {
  display: inline-block;
  vertical-align: middle;
  text-decoration: none;
  margin-right: 20px;
  min-width: 36px;
  text-align: center;
  &:last-child {
    margin-right: 0; }
  .svg-icon {
    fill: currentColor; }
  .svg-facebook {
    font-size: 36px; }
  .svg-twitter {
    font-size: 30px; }
  .svg-youtube-play {
    font-size: 26px; } }

.hidden-text {
  @include hide-text; }

.map {
  height: 445px;
  border: 1px solid rgba($color-blue, 0.3);
  border-width: 1px 0; }

.map__placeholder {
  height: 100%;
  background: img-url('map.jpg') no-repeat center;
  background-size: cover; }

.footer {
  padding: 15px 15px 20px;
  color: $color-blue;
  @include f(l, 12);
  line-height: 1.5;
  border: 1px solid rgba($color-blue, 0.3);
  @include clr; }

.footer__left {
  float: left; }

.footer__right {
  float: right; }

.partners {
  padding: 29px 0 33px;
  text-align: center; }

.partners__title {
  margin-bottom: 16px;
  @include f(l, 12, 180);
  line-height: 1;
  text-transform: uppercase;
  color: $color-blue; }

.partners__item {
  display: inline-block;
  vertical-align: middle;
  &:not(:last-child) {
    margin-right: 25px; } }


.site-maker {
  @include clr;
  > img {
    float: left;
    margin: 4px 12px 0; } }
